<template>
  <div class="container">
    <div class="row">
      <div class="col-md-3 col-sm-3">
        <div class="left bg-color">
          <h3>全部产品</h3>
          <div v-for="product in productList" :key="product.id" class="list-group"> 
            <p class="list-group-item active">{{ product.title }}</p>
            <ul class="list-unstyled">
              <li v-for="item in product.list" :key="item.id" class="list-group-item">
                <a :href="item.url">{{ item.name }}</a>
                <span v-if="item.hot" class="hot-tag">HOT</span>
              </li>
            </ul>
            <hr v-if="!product.last" class="hr">
          </div>

          <div class="list-group">
            <h3>最新消息</h3>
            <div class="list-group">
              <ul class="list-unstyled">
                <li v-for="item in newsList" :key="item.id" class="list-group-item">
                  <a :href="item.url" class="new-item">{{ item.title }}</a>
                </li>
              </ul>
            </div>
          </div>
        </div>

      </div>

      <div class="col-md-9 col-sm-9">
        <div class="row">
          <div class="col-md-12">
            <slide-show></slide-show>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6" v-for="(item, index) in boardList" :key="item.id">
            <div class="media bg-color" :class="[{'line-last': index % 2 !== 0}, {'line-first': index % 2 === 0}]">
              <div class="media-left">
                <img class="media-object" :src="item.imgSrc" alt="one">
              </div>
              <div class="media-body">
                <h4 class="media-heading">{{ item.title }}</h4>
                <p>{{ item.description }}</p>
                <router-link class="btn btn-success" :to="{ path: '/detail/' + item.toKey }">立即购买</router-link>
              </div>
            </div>              
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import slideShow from '../components/slideShow'

export default {
  components: {
    slideShow
  },
  created () {
    this.$http.get('api/seller')
    .then((res) => {
      this.newsList = res.data.data
    }, (err) => {
      console.log(err)
    })
  },
  data () {
    return {
      productList: {
        pc: {
          title: 'PC产品',
          list: [
            {
              name: '数据统计',
              url: 'http://starcraft.com'
            },
            {
              name: '数据预测',
              url: 'http://warcraft.com'
            },
            {
              name: '流量分析',
              url: 'http://overwatch.com',
              hot: true 
            },
            {
              name: '广告发布',
              url: 'http://hearstone.com'
            }
          ]
        },
        app: {
          title: '手机应用产品',
          last: true,
          list: [
            {
              name: '91助手',
              url: 'http://weixin.com'
            },
            {
              name: '产品助手',
              url: 'http://twitter.com',
              hot: true
            },
            {
              name: '智能地图',
              url: 'http://maps.com'
            },
            {
              name: '团队语音',
              url: 'http://phone.com'
            }
          ]
        }
      },
      newsList: [],
      boardList: [
        {
          title: '开放产品',
          description: '开放产品是一款开放产品',
          id: 'car',
          toKey: 'analysis',
          saleout: false,
          imgSrc: require('../assets/images/1.png')
        },
        {
          title: '品牌营销',
          description: '品牌营销帮助你的产品更好地找到定位',
          id: 'earth',
          toKey: 'count',
          saleout: false,
          imgSrc: require('../assets/images/2.png')
        },
        {
          title: '使命必达',
          description: '使命必达快速迭代永远保持最前端的速度',
          id: 'loud',
          toKey: 'forecast',
          saleout: true,
          imgSrc: require('../assets/images/3.png')
        },
        {
          title: '勇攀高峰',
          description: '帮你勇闯高峰，到达事业的顶峰',
          id: 'hill',
          toKey: 'publish',
          saleout: false,
          imgSrc: require('../assets/images/4.png')
        }
      ]
    }
  }
}
</script>

<style scoped>
.left h3 {
  color: #fff;
  background-color: #4fc08d;
  padding: 5px 10px;
  margin-bottom: 20px;
}
.left a {
  color: black;
}
.hot-tag {
  color: #fff;
  background-color: red;
}
.bg-color {
  background-color: #fff;
}
.media {
  margin-bottom: 20px;
  padding: 5px;
}
.new-item {
  width: 120px;
  overflow: hidden;
  display: inline-block;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/* .line-last {
  margin-right: -15px;
}
.line-first {
  margin-left: -15px;
} */
</style>



